/* Minimum aspect ratio */
@media (min-aspect-ratio: 8/9) {
  #root {
    background: green;
    .game-board {
      width: 92vh;
      aspect-ratio: 1/1;
    }
  }
}

/* Maximum aspect ratio */
@media (max-aspect-ratio: 2/2) {
  #root {
    background: cyan;  
    .game-board {
      width: 95%;
      aspect-ratio: 1/1;
    }
  }
}

/* Exact aspect ratio, put it at the bottom to avoid override*/
@media (aspect-ratio: 1/1) {
  #root {
    background: #f9a; /* red */
  }
}


html, body {
  margin:0px;
  height:100%;
  min-height: 175px;
  min-width: 175px;
  background-color: navy;
  overscroll-behavior: none;
}

tr {
  width: 100%;
}

td {
  width: 25%;
  height: 25%;
}

#root {
  width: 100%;
  height: 100%;
}

.info {
  z-index: 2;
  text-align: center;
  height: 1.5em;
  font-size: 1em;
  background-color: dimgray;
  user-select: none;
}

.game-board {
  position: absolute;
  left: 50%;
  min-width: 100px;
  margin-top: 1%;
  text-align: center;
  transform: translate(-50%);
  user-select: none;
}

.square {
  aspect-ratio: 1/1;
  width: 100%;
  height: 100%;
  background-color: aquamarine;
  user-select: none;
}